export const validateFields = (amount, note) => {
    const errors = {};
  
    const amountRegex = /^[0-9]+(\.[0-9]{1,2})?$/; 
    if (!amount) {
      errors.amount = " ";
    } else if (!amountRegex.test(amount)) {
      errors.amount = "   (: 50  50.75)";
    } else if (parseFloat(amount) > Number.MAX_SAFE_INTEGER) {
      errors.amount = "  ";
    }
    if (!note) {
      errors.note = " ";
    } else if (note.length > 50) {
      errors.note = "    -50 ";
    }
  
    return errors;
};
  